@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
.calc {
  background: var(--color-dark);
  color: #fff;
  max-width: 50rem;
  margin: 0 auto;
  border-top-left-radius: 0.5rem;
  border-top-right-radius: 0.5rem;
  box-shadow: rgba(0, 0, 0, 0.15) 3px 3px 5px;
}

.calc__display {
  font-size: 1.9rem;
  padding: 2.5em 2rem 1.5em;
  text-align: right;
}

.calc__btns-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  border: 0.1rem solid rgba(255, 255, 255, 0.3);
}

.calc__btn {
  cursor: pointer;
  border: none;
  border: 0.1rem solid rgba(255, 255, 255, 0.3);
  background: var(--color-light);
  color: var(--color-dark);
  padding: 1.5em 2rem;
}

.btn--span-2 {
  grid-column: span 2;
}

.btn--colored {
  background: var(--color-blue-shade);
  color: var(--color-dark);
}

*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;

  /* border: 1px solid #f2e9e4; */
}

:root {
  --color-background: #22223b;
  --color-dark: #4a4e69;
  --color-brown-shade: #9a8c98;
  --color-brown-light: #c9ada7;
  --color-blue-shade: #c3c6d5;
  --color-light: #f2e9e4;
}

html {
  font-size: 62.5%;
  font-family: 'Poppins', sans-serif;
  background: #22223b;
  background: var(--color-background);
}

body {
  font-size: 1.6rem;
}

#root {
  padding-top: 15rem;
}

